<template>
    <div class="col-2 position-fixed mt-3">
        <div class="row">
            <div class="col-12 mt-3 mb-3">
                <div class="logo d-flex align-items-center justify-content-center">
                    <a href="/">
                        <img src="../../assets/logo.png" alt="" style="width:40px;"/>
                    </a>
                </div>
            </div>
            <div class="col">
                <nav class="nav flex-column text-white">
                    <a v-smooth-scroll :href="link.link"
                       :class="{ activeLink: link.component === activeItem}"
                       @click="selectItem(link.component)"
                       class="nav-link mb-045 link-size d-flex px-0 align-items-center link-color"
                       v-for="link in this.pages" :key="link.component">
                        <div class="flex-grow-1 text-end me-3">
                            {{ link.title }}
                        </div>
                        <div id="diamond">
                        </div>
                    </a>

                </nav>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    props: ['pages'],
    data() {
        return {
            activeItem: null,
        }
    },
    methods: {
        selectItem(item) {
            this.activeItem = item;
        },
    }
}
</script>
<style>
.activeLink {
    color: #ff0000 !important;
    transition: 1s!important;
}

.activeLink #diamond {
    border-bottom-color: #ff0000 !important;
    transition: 1s!important;
}
.activeLink #diamond:after {
    border-top-color: #ff0000 !important;
    transition: 1s!important;
}

.mb-045 {
    margin-bottom: .45rem;
}

.link-size {
    font-size: 0.75rem;
}

.link-color {
    color: rgba(255, 255, 255, 0.4);
}

#diamond {
    transform: scale(0.6);
    width: 0;
    height: 0;
    border: 8px solid transparent;
    border-bottom-color: rgba(255, 255, 255, 0.4);
    position: relative;
    top: -4px;
}
#diamond:after {
    content: '';
    position: absolute;
    left: -8px;
    top: 8px;
    width: 0;
    height: 0;
    border: 8px solid transparent;
    border-top-color: rgba(255, 255, 255, 0.4);
}
</style>